/*Table of content

	- HTML / Body
  - Heading
  - Anchor
	- margin
	- Padding
	- Wide
	- Border radius

*/

html, body {
	height: 100%;
	margin: 0;
	padding: 0;
}

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  height: 100%;
  font-size: 62.5%;
}

body {
	font-size: 14px;
	@include body-font;
	font-weight: 300;
	color: $base-color;
	background: #ECF1F5;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
	@include heading-font;
}

a {
	color: $color-main;
	&:hover {
		color: $base-color;
		text-decoration: none;
	}
  &:focus {
    outline: 0;
  }
}

/*
* Margin
*/
.margin {
	&-60 {
		margin: 60px;
	}

	&-50 {
		margin: 50px;
	}

	&-40 {
		margin: 40px;
	}

	&-30 {
		margin: 30px;
	}

	&-20 {
		margin: 20px;
	}

	&-10 {
		margin: 10px;
	}

	&-0 {
		margin: 0px;
	}
}

.margintop {
	&-40 {
		margin-top: 40px;
	}
	&-30 {
		margin-top: 30px;
	}
	&-20 {
		margin-top: 20px;
	}
	&-10 {
		margin-top: 10px;
	}
	&-0 {
		margin-top: 0px;
	}
}

.marginbot {
	&-40 {
		margin-bottom: 40px;
	}
	&-30 {
		margin-bottom: 30px;
	}
	&-20 {
		margin-bottom: 20px;
	}
	&-10 {
		margin-bottom: 10px;
	}
}

.margintb {
	&-60 {
		margin: 60px 0;
	}

	&-50 {
		margin: 50px 0;
	}

	&-40 {
		margin: 40px 0;
	}

	&-30 {
		margin: 30px 0;
	}

	&-20 {
		margin: 20px 0;
	}

	&-10 {
		margin: 10px 0;
	}

	&-0 {
		margin: 0px 0px;
	}
}

/*
* Padding
*/
