		padding: 60px;
	}

	&-50 {
		padding: 50px;
	}

	&-40 {
		padding: 40px;
	}

	&-30 {
		padding: 30px;
	}

	&-20 {
		padding: 20px;
	}

	&-10 {
		padding: 10px;
	}

	&-0 {
		padding: 0px;
	}
}

.paddingtb {
	&-60 {
		padding: 60px 0px;
	}

	&-50 {
