	}

	&-20 {
		padding: 20px 0px;
	}

	&-10 {
		padding: 10px 0px;
	}

	&-0 {
		padding: 0px 0px;
	}
}

.paddingtop {
	&-40 {
		padding-top: 40px;
	}
	&-30 {
		padding-top: 30px;
	}
	&-20 {
		padding-top: 20px;
	}
	&-10 {
		padding-top: 10px;
	}
	&-0 {
		padding-top: 0px;
	}
}

.paddingbot {
	&-40 {
		padding-bottom: 40px;
	}
	&-30 {
		padding-bottom: 30px;
	}
	&-20 {
		padding-bottom: 20px;
	}
	&-10 {
		padding-bottom: 10px;
	}
	&-0 {
		padding-bottom: 0px;
	}
}

/*
* Fullwidth
*/
.fullwide {
	.row {
		margin-left: 0;
		margin-right: 0;
	}
	div[class*="col-"] {
		padding-left: 0;
		padding-right: 0;
	}
}

/*
* Border radius
*/
.bradius-small {
	border-radius: 3px
}

.bradius-medium {
	border-radius: 5px
}

.no-bradius {
	border-radius: 0!important;
}
