// -----------------------------------------------------------------------------
// Box
// -----------------------------------------------------------------------------
.unibox {
  background: #fff;
  border-radius: 7px;
  text-align: center;
  @include transition(all 0.2s ease-in-out);
}

.unibox__border {
  border: 1px solid #e4e4e4;
}

.unibox__frame {
  position: relative;
  min-height: 400px;
  overflow: hidden;

  &:before {
    background: rgba(0,0,0,.2);
    display: block;
    content: "";
    width: 100%;
    position: absolute;
    height: 100%;
    z-index: 2;
  }

  .uniboxinner {
    position: relative;
    overflow: hidden;

    .unimages {
      padding-bottom: 135%;
      position: relative;
      height: 0;
      overflow: hidden;
      display: block;
      z-index: 1;

      img {
        @include imgbase;
      }
    }

    .uninfo {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 3;
      width: 100%;

      h3, p {
        color: #f3f3f3;
      }
    }
  }

  &:hover {
    &:before {
      background: rgba(0,0,0,.3);
    }
  }
}

.unibox__boxset {
  .unitag {
    position: absolute;
    top: 20px;
    z-index: 4;
    right: 0;
    background: #fff;
    padding: 5px 10px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
  }

  .uniboxinner {
    .uninfo {
      top: auto;
      bottom: 0;
      transform: translate(-50%, 40%);
      transition: all ease .3s;
    }
  }

  &:hover {
    &:before {
      background-image: linear-gradient(to top, rgba(0,0,0,2) 20%, rgba(0,0,0,0.2));
    }

    .uniboxinner {
      .uninfo {
        top: auto;
        bottom: 0;
        transform: translate(-50%,-50%);
        transition: all ease .3s;
      }
    }
  }
}

.unibox__uniform {
  position: relative;

  .uninfo {
    opacity: 0;
    transform: translate(-50%, 0%);
  }

  &:before {
    position: absolute;
    content: "";
    top: 50px;
    left: 0;
    background: #3f77fa;
    height: 100%;
    width: 100%;
    text-align: center;
    opacity: 0;
    border-radius: 5px;
    opacity: 0;
    transition: all 0.3s ease-in-out;
  }

  &:hover {
    &:before {
      opacity: 0.8;
      top: 0;
      background: #3f77fa;
    }

    .uninfo {
      opacity: 1;
      transform: translate(-50%, -50%);
    }
  }
}

// -----------------------------------------------------------------------------
// Blog
// -----------------------------------------------------------------------------
.uniblog {
  overflow: hidden;

  .unibloginner {
    .unimages {
      padding-bottom: 65%;
      position: relative;
      height: 0;
      overflow: hidden;
      display: block;
      z-index: 1;

      img {
        @include imgbase;
      }
    }
  }
}

.uniblog__uno {
  .unibloginner {
    h3 {
      position: relative;

      &:before {
        content: "";
        position: absolute;
        z-index: 1;
        left: 0;
        top: -15px;
        width: 0;
        height: 2px;
        background: #c69c5b;
        transition: all .3s ease-in-out;
      }
    }
  }
  &:hover {
    .unibloginner {
      h3 {
        &:before {
          width: 50px;
          transition: all .3s ease-in-out;
        }
      }
    }
  }
}

.uniblog__duo {
  .unimages {
    transition: all .5s ease;
  }
  &:hover {
    .unimages {
      transform: scale(1.05) translate(0,-2.5%);
      transform-origin: center;
    }
  }
}

.box-list {
	margin: 0 0 40px 0;
  padding: 0 0 40px 0;
  border-bottom: 1px solid $color-purple;

	h4 {
		font-size: 32px;
	}

	img, h4 {
		margin: 0 0 20px 0;
	}

	.meta {
		padding: 10px 0;

		span {
			margin: 0 20px 0 0;
			font-size: 12px;
		}
		i {
			margin: 0 10px 0 0;
			color: $base-color;
		}

		a {
			color: $color-purple;
		}
	}
}
.boxflex {
	display: -webkit-flex;
 	display: flex;

	.dfinner {
		justify-content: center;
		align-items: center;

		a {
			color: $color-purple;

			&:hover {
				color: #000;
			}
		}

		&:first-child {
			width: 40%;

			img {
		    display: block;
		    object-fit: cover;
		    width: 100%;
		    height: 100%;
			}
		}

		&:last-child {
			width: 60%;
			padding: 20px;
		}
	}
}

@media(min-width: 768px){
	.boxflex {
		.dfinner {
			height: 400px;
		}
	}
}

@media(max-width: 991px){
	.boxflex {
		flex-direction: column;

		.dfinner {
			width: 100%!important;
		}
	}
}

// -----------------------------------------------------------------------------
// Testimonial
// -----------------------------------------------------------------------------
.unitesti {
  background: transparent;

  .unitestimg {
    margin: -40px auto;
    height: 60px;
    width: 60px;

    img {
      height: 100%;
    }
  }

  .unitesti-info {
    background: $color-white;
    border: 1px solid #ecf0f5;
    border-radius: 8px;
    transition: all .3s ease-in-out;
    box-shadow: 0 0 1.25rem rgba(108,118,134,.1)!important;

    p {
      padding: 60px 25px 30px;
      line-height: 24px;
      font-style: italic;
    }
  }
}

.testimo {
    position: relative
}

.testimo .testimo-arrow {
    position: absolute;
    width: 12px;
    height: 12px;
    bottom: -6px;
    border: 2px solid #e6e6e6;
    border-top: none;
    border-left: none;
    left: calc(50% - 6px);
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    left: 22px
}

.testimo .testimo-content {
  position: relative;
  border: 2px solid #e6e6e6;
  border-bottom: none;
  padding: 40px 30px;
  margin-bottom: 30px
}

.testimo .testimo-content p {
    margin-bottom: 0
}

.testimo .testimo-content:after,.testimo .testimo-content:before {
    background-color: #e6e6e6!important;
    content: "";
    position: absolute;
    width: calc(50% - 8px);
    height: 2px;
    bottom: 0;
    background-color: #e6e6e6
}

.testimo .testimo-content:before {
    width: 21.5px;
    left: 0
}

.testimo .testimo-content:after {
    width: calc(100% - 35.5px);
    right: 0
}

.testimo .img-testimo {
    width: 60px;
    max-width: 100%;
    overflow: hidden;
    position: relative;
    vertical-align: top;
    display: inline-block;
    border-radius: 50%;
    -webkit-border-radius: 50%
}

.testimo .img-testimo img {
    top: 50%;
    left: 50%;
    width: auto;
    height: 100%;
    max-width: none;
    position: absolute;
    transform: translate(-50%,-50%)
}

.testimo .img-testimo:before {
    display: block;
    content: "";
    padding-top: 100%
}

.testimo .wrapper-info {
    max-width: calc(100% - 85px);
    padding-left: 15px;
    display: inline-block
}

.testimo .wrapper-info .name,.testimo .wrapper-info .position {
    display: inline-block
}
