// -----------------------------------------------------------------------------
// Footer
// -----------------------------------------------------------------------------
footer {
	.footer-inner {
		padding: 65px 0;
		background: #212121;
		color: #a2a2a2;
		background-image: url("/img/rawpixel-com-252130.jpg");
		position: relative;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 0 90%;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0, 6, 16, 0.8);
		}

		.recent {
			ul {
				list-style: none;
				padding-left: 0;
				li {
					border-bottom: 1px dotted #404040;
					a {
						padding: 6px 0;
						display: inline-block;
						width: 100%;
						position: relative;
						color: #a2a2a2;
						&:before {
							content: "";
					    display: inline-block;
					    height: 48%;
					    position: absolute;
					    left: -10px;
					    border-left: 3px solid $base-color;
					    top: 50%;
					    -webkit-transform: translateY(-50%);
					            transform: translateY(-50%);
					    -webkit-transition: opacity .15s ease-out;
					    transition: opacity .15s ease-out;
					    opacity: 0;
						}
						&:hover {
							color: #fff;
							&:before {
								opacity: 1;
							}
						}
					}
				}
			}
		}
	}

	.footer-logo {
		img {
			height: 40px;
		}
	}

	.heading-section {
		h2, h4 {
			color: $color-white;
			&:after {
				right: calc(50% - 35px);
				left: 0;
				width: 45px;
			}
		}
	}

	.footer-box {
		&.address {
			.item {
				i {
					position: relative;
					line-height: 24px;
					left: 0;
					display: table-cell;
			    width: 30px;
			    text-align: left;
				}
				.contact-info {
					display: table-cell;
					.text {
						display: block;
						margin: 0;
					}
				}
			}
		}
	}

	.social {
		margin-top: 25px;
		a {
			display: inline-block;
			vertical-align: top;
			margin: 0 10px 10px 0;
			font-size: 14px;
			line-height: 24px;
			width: 40px;
			text-align: center;
			padding: 8px 10px;
			border-radius: 2px;
			-webkit-border-radius: 2px;
			background-color: rgba(0, 0, 0, 0.1);
			position: relative;
			overflow: hidden;
			color: $color-white;
			&:before {
				position: absolute;
				content: '';
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				-webkit-transform-origin: right;
				-ms-transform-origin: right;
				transform-origin: right;
				-webkit-transform: scale(0, 1);
				-ms-transform: scale(0, 1);
				-o-transform: scale(0, 1);
				transform: scale(0, 1);
				background-color: $base-color;
				-webkit-transition: transform 0.4s ease;
				-o-transition: transform 0.4s ease;
				transition: transform 0.4s ease;
				z-index: 1;
			}
			&:hover {
				&:before {
					-webkit-transform-origin: left;
			    -ms-transform-origin: left;
			    transform-origin: left;
			    -webkit-transform: scale(1, 1);
			    -ms-transform: scale(1, 1);
			    -o-transform: scale(1, 1);
			    transform: scale(1, 1);
				}
			}
			i {
				position: relative;
				z-index: 2;
			}
		}
	}

	.copyright {
		display: inline-block;
		width: 100%;
		background-color: $color-dark;
		vertical-align: middle;
		text-align: center;
		padding: 30px 0;
		color: $color-white;

		a {
			color: $base-color;

			&:hover {
				color: $color-white;
			}
		}
	}
}
