// base.scss
@mixin body-font {
  font-family: "rubikregular", sans-serif;
}

@mixin heading-font {
  font-family: "rubikmedium", sans-serif;
}

/* base color */
$base-color: #0F1C33;
$color-main: #10c5a7;
$color-secondary: #2F4051;

$base-link: #ff585d;
$base-link-hover: #ff585d;

$color-teal: #efebe5;
$color-purple: #526383;

/* used color */
$color-green: #00AB4F;
$color-blue: #0C92F1;
$color-lightblue: #4E9CEA;
$color-lightpurple: #F3EEFC;
$color-calmpurple: #E9E2F6;
$color-white: #ffffff;
$color-calmgrey: #F2F2F2;
$color-lightgrey: #DCDCDC;
$color-dark: #000610;
