/*Table of content

	- Button
  - Heading

*/

// -----------------------------------------------------------------------------
// Button section
// -----------------------------------------------------------------------------
.unibtn {
  border: 0;
  color: $color-white;
  padding: 1rem 2rem;
  font-size: 12px;

  &:hover {
    color: $color-white;
  }
}

.unibtn__arrow {
  &:before {
    font-family: 'themify';
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: "\e628";
    margin-right: 5px;
    transition: all 0.4s ease-in-out 0s;
  }

  &:hover {
    &:before {
      margin-right: 15px;
      margin-left: 5px;
      transition: all 0.4s ease-in-out 0s;
    }
  }
}

.unibtn__background {
  background-color: transparent;
  color: $base-color;
  border: 1px solid $base-color;

  &:hover {
    color: $color-white;
    background-color: $color-secondary;
  }
}


// -----------------------------------------------------------------------------
// Heading section
// -----------------------------------------------------------------------------
.uniheading {
	h1, h2, h4 {
		position: relative;
		padding-bottom: 10px;
		margin-bottom: 10px;
		text-transform: uppercase;
		&:after {
			content: "";
			position: absolute;
			bottom: 0;
			right: calc(50% - 35px);
			left: calc(50% - 35px);
			height: 1px;
			width: 75px;
			background: $base-color;
		}
	}

	&.heading-padspace {
		padding: 40px 0;
	}
}

// -----------------------------------------------------------------------------
// Image section
// -----------------------------------------------------------------------------
.unimage {
  padding-bottom: 100%;
  position: relative;
  height: 0;
  overflow: hidden;
  display: block;
  z-index: 1;

  img {
    @include imgbase;
  }
}
