/*Table of content

	- Transform up

*/
.transform:hover {
  box-shadow: -1px 9px 18px 0px rgba(75, 81, 91, 0.1);
  transform: translateY(-0.5em);
}

.transform__uno:hover {
  box-shadow: 0 5px 10px rgba(0, 9, 128, 0.035), 0 7px 18px rgba(0, 9, 128, 0.05);
  transform: translateY(-0.25em);
}

.unilink span {
  position: relative;
  display: inline-block;
}

.unilink span:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -5px;
  left: 0;
  background-color: red;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out 0s;
}

.unilink span:hover:before {
  visibility: visible;
  transform: scaleX(1);
}

.bgcolor {
  background: linear-gradient(-45deg, #4e54c8, #8089ff);
}

/*Table of content

	- Button
  - Heading

*/
.unibtn {
  border: 0;
  color: #ffffff;
  padding: 1rem 2rem;
  font-size: 12px;
}

.unibtn:hover {
  color: #ffffff;
}

.unibtn__arrow:before {
  font-family: 'themify';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\e628";
  margin-right: 5px;
  transition: all 0.4s ease-in-out 0s;
}

.unibtn__arrow:hover:before {
  margin-right: 15px;
  margin-left: 5px;
  transition: all 0.4s ease-in-out 0s;
}

.unibtn__background {
  background-color: transparent;
  color: #0F1C33;
  border: 1px solid #0F1C33;
}

.unibtn__background:hover {
  color: #ffffff;
  background-color: #2F4051;
}

.uniheading h1, .uniheading h2, .uniheading h4 {
  position: relative;
  padding-bottom: 10px;
  margin-bottom: 10px;
  text-transform: uppercase;
}

.uniheading h1:after, .uniheading h2:after, .uniheading h4:after {
  content: "";
  position: absolute;
  bottom: 0;
  right: calc(50% - 35px);
  left: calc(50% - 35px);
  height: 1px;
  width: 75px;
  background: #0F1C33;
}

.uniheading.heading-padspace {
  padding: 40px 0;
}

.unimage {
  padding-bottom: 100%;
  position: relative;
  height: 0;
  overflow: hidden;
  display: block;
  z-index: 1;
}

.unimage img {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  max-width: inherit !important;
  object-fit: cover;
}

