.unilink {
  span {
    position: relative;
    display: inline-block;
    &:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 2px;
      bottom: -5px;
      left: 0;
      background-color: red;
      visibility: hidden;
      transform: scaleX(0);
      transition: all 0.3s ease-in-out 0s;
    }

    &:hover {
      &:before {
        visibility: visible;
        transform: scaleX(1);
      }
    }
  }
}
